<template>
    <div>
        <!-- 标题 -->
        <div class="title">
            <van-nav-bar title="选择专属你的户门"/>
        </div>
        <!-- 内容 -->
        <div class="content">
            <!-- 步骤条 -->
            <div class="stepbar">
                <van-steps :active="active">
                    <van-step>款式选择</van-step>
                    <van-step>锁体选择</van-step>
                    <van-step>选择户门</van-step>
                    <van-step>效果查看</van-step>
                </van-steps>
            </div>
            <!-- 效果展示 -->
            <div class="show">
                <van-swipe :autoplay="3000">
                    <van-swipe-item v-for="(image, index) in images" :key="index">
                        <img :src="image.img" />
                    </van-swipe-item>
                </van-swipe>
            </div>
        </div>
        <!-- 底部 -->
        <div class="footer">
            <van-submit-bar
                :price="0"
                button-text="文字……"
            />
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            active: 3,
            images: [
                {img: 'http://www.olalocks.com/images/door_05_01.jpg'},
                {img: 'http://www.olalocks.com/images/door_05_02.png'},
            ]
        }
    },
    methods: {
        
    },
}
</script>

<style lang="less" scoped>
.title{
    width: 100%;
    height: 50px;
    line-height: 50px;
    border-bottom: 1px solid #ccc;
    display: flex;
    .van-nav-bar{
        width: 100%;
    }
}
.content{
    .stepbar{
        width: 90%;
        margin: 5px auto;
    }
    .show{
        img{
            width: 100%;
        }
    }
}
.footer{
    .van-submit-bar__bar{
        border-top: 1px solid #ccc;
    }
}
.van-button--danger{
    background: rgb(7, 193, 96);
    border: 1px solid rgb(7, 193, 96);
}
.van-submit-bar__price{
    color: rgb(7, 193, 96);
}
</style>


